.about-container {
  display: flex;
  flex-direction: column;
  margin: 1rem;
}

.about-content {
  &__title {
    font-size: xx-large;
    font-weight: bold;
    padding: 0;
    margin: 0;
  }
  &__description {
    font-size: x-large;
    font-weight: bold;
    padding: 0;
    margin-top: 0.5rem;
  }
}
